<template>
<el-container>
  <el-header>
    <el-row>
      <el-col :span="24" :sm="{span: 10, offset: 7}" :md="{span: 8, offset: 8}" :lg="{span: 6, offset: 9}">
        <div class="header-title"><i class="el-icon-upload"></i>项目授权管理</div>
      </el-col>
    </el-row>
  </el-header>
  <el-main>
    <el-row>
      <el-col :span="24" :sm="{span: 10, offset: 7}" :md="{span: 8, offset: 8}" :lg="{span: 6, offset: 9}">
        <div>
          <el-input placeholder="请输入授权人" v-model="oprator" clearable>
            <template slot="prepend">授权人：</template>
          </el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" :sm="{span: 10, offset: 7}" :md="{span: 8, offset: 8}" :lg="{span: 6, offset: 9}">
        <div style="margin-top: 15px;">
          <el-input placeholder="请输入用户名" v-model="username" clearable>
            <template slot="prepend">用户名：</template>
          </el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" :sm="{span: 10, offset: 7}" :md="{span: 8, offset: 8}" :lg="{span: 6, offset: 9}">
        <admin-select :data="projectData" :prepend="'项目名称'" :tip="'项目'" @selectItem="handleProjectSelected"></admin-select>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" :sm="{span: 10, offset: 7}" :md="{span: 8, offset: 8}" :lg="{span: 6, offset: 9}">
        <admin-select :data="timeData" :prepend="'授权时间'" :tip="'时间'" @selectItem="handleTimeSelected"></admin-select>
      </el-col>
    </el-row>
  </el-main>
  <el-footer>
    <el-row>
      <el-col :span="24" :sm="{span: 10, offset: 7}" :md="{span: 8, offset: 8}" :lg="{span: 8, offset: 8}">
        <div style="margin-top: 40px;">
          <el-button-group>
            <el-button type="primary" plain @click="updateUser">增加时间</el-button>
            <el-button type="primary" plain @click="queryUser">查询用户</el-button>
            <el-button type="danger" plain @click="deleteUser">删除用户</el-button>
          </el-button-group>
        </div>
      </el-col>
    </el-row>
  </el-footer>
</el-container>
</template>

<script>
import AdminSelect from './adminSelect.vue'
import projectData from './mocks/porjectData.json'
import timeData from './mocks/timeData.json'

export default {
  name: 'admin',
  components: { AdminSelect },
  data () {
    return {
      oprator: '',
      username: '',
      project: '',
      time: '',
      projectData: [],
      timeData: []
    }
  },
  methods: {
    handleProjectSelected (value) {
      this.project = value
    },
    handleTimeSelected (value) {
      this.time = value
    },
    checkOprator () {
      return this.oprator || (this.showErr('请填写授权人'), false)
    },
    checkUsername () {
      return this.username || (this.showErr('请填写用户名'), false)
    },
    checkProjet () {
      return this.project || (this.showErr('请选择项目'), false)
    },
    checkTime () {
      return this.time || (this.showErr('请选择授权时间'), false)
    },
    checkParamsComplete (action) {
      switch (action) {
        case 'update':
          return this.checkOprator() && this.checkUsername() && this.checkProjet() && this.checkTime()
        case 'query':
          return this.checkOprator() && this.checkUsername() && this.checkProjet()
        case 'delete':
          return this.checkOprator() && this.checkUsername() && this.checkProjet()
        default:
          return false
      }
    },
    updateUser () {
      if (this.checkParamsComplete('update')) {
        this.$axios.post('/admin/' + this.project, {
          action: 'update',
          admin: this.oprator,
          username: this.username,
          time: this.time
        })
        .then(res => res.data)
        .then(res => {
          if (res.errno === 0) {
            this.showSuccess('操作成功')
          } else {
            this.showErr(res.errmsg)
          }
        }, err => {
          this.showErr(err.message)
        })
      }
    },
    queryUser () {
      if (this.checkParamsComplete('query')) {
        this.$axios.post('/admin/' + this.project, {
          action: 'query',
          admin: this.oprator,
          username: this.username
        })
        .then(res => res.data)
        .then(res => {
          if (res.errno === 0) {
            this.showSuccess('操作成功。到期时间：' + new Date(parseInt(res.data.time)).toLocaleString())
          } else {
            this.showErr(res.errmsg)
          }
        }, err => {
          this.showErr(err.message)
        })
      }
    },
    deleteUser () {
      if (this.checkParamsComplete('delete')) {
        this.$confirm('此操作将永久删除用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$axios.post('/admin/' + this.project, {
            action: 'delete',
            admin: this.oprator,
            username: this.username
          })
          .then(res => res.data)
          .then(res => {
            if (res.errno === 0) {
              this.showSuccess('操作成功')
            } else {
              this.showErr(res.errmsg)
            }
          }, err => {
            this.showErr(err.message)
          })
        }).catch(() => {
          this.showErr('取消操作')
        })
      }
    },
    showErr (err) {
      this.$message({
        type: 'error',
        message: '操作失败: ' + err
      })
    },
    showSuccess (msg) {
      this.$message({
        type: 'success',
        message: msg
      })
    }
  },
  mounted () {
    this.projectData = projectData
    this.timeData = timeData
  }
}
</script>

<style>
.header-title {
  background-color: #409EFF;
  font-size: 20px;
  line-height: 36px;
  border-radius: 4px;
  min-height: 36px;
}
</style>



