<template>
  <div style="margin-top: 15px;">
    <el-autocomplete style="display: block;" class="inline-input" v-model="name" :fetch-suggestions="querySearch" :placeholder="'单击选择'+ tip" @select="handleSelect" @blur="handleBlur" @debounce="30" clearable>
      <template slot="prepend">{{ prepend }}：</template>
    </el-autocomplete>
  </div>
</template>

<script>
export default {
  name: 'selector',
  props: {
    data: Array,
    prepend: String,
    tip: String
  },
  data () {
    return {
      name: '',
      value: ''
    }
  },
  methods: {
    querySearch (queryString, cb) {
      cb(this.data)
    },
    handleSelect (item) {
      this.value = item.realValue
      if (this.checkSelect()) {
        this.$emit('selectItem', this.value)
      }
    },
    handleBlur () {
      if (!this.checkSelect()) {
        this.name = ''
        this.value = ''
        this.$emit('selectItem', '')
      }
    },
    checkSelect () {
      let arr = this.data.filter((item) => {
        return item.value === this.name
      })
      return arr.length > 0
    }
  }
}
</script>

<style scoped>

</style>



