import Vue from 'vue'
import Router from 'vue-router'
import Admin from '@/components/admin/admin.vue'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    redirect: '/admin'
  },
  {
    path: '/admin',
    component: Admin
  }
  ]
})



